@import '../../constants/variables';

.loader {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50;
    left: 45%;
    width: $ringSizeOuter;
    height: $ringSizeOuter;

    div {
        box-sizing: border-box;
        display: block;
        position: absolute;
        width: $ringSizeInner;
        height: $ringSizeInner;
        margin: 8px;
        border: 8px solid $tertiary;
        border-radius: 50%;
        animation: loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
        border-color: $tertiary transparent transparent transparent;
    }

    div:nth-child(1) {
        animation-delay: -0.45s;
    }

    div:nth-child(2) {
        animation-delay: -0.3s;
    }

    div:nth-child(3) {
        animation-delay: -0.15s;
    }
};

@keyframes loader {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
};
  